	-webkit-transform:rotateY(180deg);
	transform:rotateY(180deg)
}
}
.page3 .skill .skill-title {
	font-size:18px;
	width:100px;
	height:40px;
	margin:10px auto;
	border-bottom:3px solid red
}
.page3 .skill .skill-point {
	font-size:16px;
	display:inline-block;
	margin:5px 20px;
	padding:5px 10px;
	background:rgba(255,154,0,0.4);
	border-radius:8px
}
.page3 .skill .backface {
	position:absolute;
	left:0;
	top:0;
	width:100%;
	height:100%;
	padding:15px;
	text-indent:2em;
	text-align:justify;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	font-size:14px;
	-webkit-transform:rotateY(180deg) translateZ(1px);
	transform:rotateY(180deg) translateZ(1px);
	-webkit-backface-visibility:hidden;
	backface-visibility:hidden;
	overflow:hidden;
	background:#efefef
}
.page4 {
	text-align:center
