@-webkit-keyframes fadeInRight {
0% {
	opacity:0;
	-webkit-transform:translate(100%,0);
	transform:translate(100%,0)
}
100% {
	opacity:1
}
}
@keyframes fadeInRight {
0% {
	opacity:0;
	-webkit-transform:translate(100%,0);
	transform:translate(100%,0)
}
100% {
	opacity:1
}
}
@-webkit-keyframes fadeInLeft {
0% {
	opacity:0;
	-webkit-transform:translate(-100%,0);
	transform:translate(-100%,0)
}
100% {
	opacity:1
}
}
@keyframes fadeInLeft {
0% {
	opacity:0;
	-webkit-transform:translate(-100%,0);
	transform:translate(-100%,0)
}
100% {
	opacity:1
}
}
@-webkit-keyframes fadeIn {
0% {
	opacity:0
}
100% {
	opacity:1
}
}
@keyframes fadeIn {
0% {
	opacity:0
}
100% {
	opacity:1
}
}
.on .pro2,.on .pro4,.on .pro6 {
	-webkit-animation:fadeInRight .8s ease both;
	animation:fadeInRight .8s ease both
}
.on .pro1,.on .pro3,.on .pro5 {
	-webkit-animation:fadeInLeft .8s ease both;
	animation:fadeInLeft .8s ease both
}
.on .pro1 {
	-webkit-animation-delay:0s;
	animation-delay:0s
}
.on .pro2 {
	-webkit-animation-delay:0.2s;
	animation-delay:0.2s
}
.on .pro3 {
	-webkit-animation-delay:0.4s;
	animation-delay:0.4s
}
.on .pro4 {
	-webkit-animation-delay:0.6s;
	animation-delay:0.6s
}
.on .pro5 {
	-webkit-animation-delay:0.8s;
	animation-delay:0.8s
}
.on .pro6 {
	-webkit-animation-delay:1s;
	animation-delay:1s
}
@-webkit-keyframes rotate360 {
0% {
	-webkit-transform:rotateY(-360deg);
	transform:rotateY(-360deg)
}
}
@keyframes rotate360 {
0% {
	-webkit-transform:rotateY(-360deg);
	transform:rotateY(-360deg)
}
}
.on .skill {
	-webkit-animation:rotate360 1.2s ease;
	animation:rotate360 1.2s ease
}
.on .skill1 {
	-webkit-animation-delay:0s;
	animation-delay:0s
}
.on .skill2 {
	-webkit-animation-delay:0.2s;
	animation-delay:0.2s
}
.on .skill3 {
	-webkit-animation-delay:0.4s;
	animation-delay:0.4s
}
.on .skill4 {
	-webkit-animation-delay:0.6s;
	animation-delay:0.6s
}
.on .skill5 {
	-webkit-animation-delay:0.8s;
	animation-delay:0.8s
}
.on .skill6 {
	-webkit-animation-delay:1s;
	animation-delay:1s
}
@-webkit-keyframes fadeInUpBig {
from {
	opacity:0;
	-webkit-transform:translate3d(0,2000px,0);
	transform:translate3d(0,2000px,0)
}
to {
	opacity:1;
	-webkit-transform:none;
	transform:none
}
}
@keyframes fadeInUpBig {
from {
	opacity:0;
	-webkit-transform:translate3d(0,2000px,0);
	transform:translate3d(0,2000px,0)
}
to {
	opacity:1;
	-webkit-transform:none;
	transform:none
}
}
.on .end {
	-webkit-animation:fadeInUpBig 1s ease both;
	animation:fadeInUpBig 1s ease both
}
.on .end1 {
	-webkit-animation-delay:0s;
	animation-delay:0s
}
.on .end2 {
	-webkit-animation-delay:0.5s;
	animation-delay:0.5s
}
.on .end3 {
	-webkit-animation-delay:1s;
	animation-delay:1s
}
.on .end4 {
	-webkit-animation-delay:1.5s;
	animation-delay:1.5s
}
.on .end5 {
	-webkit-animation-delay:2s;
	animation-delay:2s
}
.on .end6 {
	-webkit-animation-delay:2.5s;
	animation-delay:2.5s
}
.inverse {
	-webkit-transform:rotateY(180deg);
	transform:rotateY(180deg)
}
.mouse-tips {
	position:absolute;
	width:50px;
	height:50px;
	left:calc(50% - 25px);
	bottom:20px;
	-webkit-animation:doUpDown .5s ease-in-out infinite alternate both;
	animation:doUpDown .5s ease-in-out infinite alternate both;
	color:#fff;
	font-size:50px
}
@-webkit-keyframes doUpDown {
0% {
	-webkit-transform:translate(0,0);
	transform:translate(0,0)
}
100% {
	-webkit-transform:translate(0,-80%);
	transform:translate(0,-80%)
}
}
@keyframes doUpDown {
0% {
	-webkit-transform:translate(0,0);
	transform:translate(0,0)
}
100% {
	-webkit-transform:translate(0,-80%);
	transform:translate(0,-80%)
}
}
@-webkit-keyframes doUpDown2 {
0% {
	-webkit-transform:translate(0,0) rotate(180deg);
	transform:translate(0,0) rotate(180deg)
}
100% {
	-webkit-transform:translate(0,-80%) rotate(180deg);
	transform:translate(0,-80%) rotate(180deg)
}
}
@keyframes doUpDown2 {
0% {
	-webkit-transform:translate(0,0) rotate(180deg);
	transform:translate(0,0) rotate(180deg)
}
100% {
	-webkit-transform:translate(0,-80%) rotate(180deg);
	transform:translate(0,-80%) rotate(180deg)
}
}
.mouse-tips.up {
	bottom:50px;
	-webkit-animation-name:doUpDown2;
	animation-name:doUpDown2
}
